import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';
let app = getApp().globalData;
let {
	appid,
	navBar,
	appsecret,
	rootUrl,
	imgUrl
} = app;
export default {
	data() {
		return {
			imgUrl: app.imgUrl,
			img: [],
			contents: '',
			navBar: navBar,
			count:0,
		};
	},
	methods: {
		leftClick() {





			uni.navigateBack({
				delta: 1
			});

		},
		deletePhoto(e) {

			let {
				img
			} = this;
			img.splice(e, 1);
		},
		uploadImg() {
			var that = this;
			uni.chooseImage({
				count: 1, // 默认9
				sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
				success: function(res) {
					// 返回选定照片的本地文件路径列表，tempFilePath可以作为img标签的src属性显示图片
					var media_id = res.tempFilePaths;

					var feed = 'feed';
					var timeStamp = Math.round(new Date().getTime() / 1000);
					var obj = {
						appid: appid,
						type: feed,
						timeStamp: timeStamp
					};

					var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
					for (var i = 0; i < media_id.length; i++) {
						uni.uploadFile({
							method: 'POST',
							url: rootUrl + '/mobileOrder/uploadImg', //此处换上你的接口地址
							name: 'img',
							header: {
								Authorization: uni.getStorageSync('cdj_token')
							},
							formData: {
								appid: appid,
								timeStamp: timeStamp,
								type: feed,
								img: media_id,
								sign: sign
							},
							filePath: media_id[i],
							success: function(res) {
								var img = JSON.parse(res.data);
								if (img.code == 200) {
									that.img.push(img.data);
								} else if (img.code == 401) {
									rs.Toast('请先登录')
									setTimeout(() => {
										uni.navigateTo({
											url: '/pages/account/login'
										})
									}, 1000)
									
								} else {
									rs.Toast(img.msg)
								}

							}
						});
					}
				}
			});
		},
		feedBack() {
			this.count++;
			if(this.count!=1)return;
			setTimeout(()=>{this.count=0},1000)
			var that = this
			var img = that.img;
			let path = [];
			for (let i of img) {
				path.push(i.path)
			}
			console.log(path)
			var contents = that.contents;
			var timeStamp = Math.round(new Date().getTime() / 1000);

			var obj = {
				appid: appid,
				contents: contents,
				timeStamp: timeStamp,
			}
			var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			var data = {
				appid: appid,
				contents: contents,
				img: path,
				timeStamp: timeStamp,
				sign: sign,
			}
			rs.postRequests("feedBack", data, (res) => {
				if (res.data.code == 200) {
					rs.Toast('提交成功')
					setTimeout(() => {
						uni.switchTab({
							url: '/pages/tabar/index',
						})
					}, 1000)
				} else {
					rs.Toast(res.data.msg)
				}
			})
		}
	},
	onShow(){
		uni.setNavigationBarTitle({
			    title: uni.getStorageSync('titleKey')
			});
	}

};
